import React, { Component } from "react";
import './reactLogo.css';
import './index.css';
import TodoItems from './TodoItems';

class TodoList extends Component {
  constructor(props) {
    super(props);

    this.state = {
      items: []
    };

    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
  }
  addItem(e) {
      if(this._inputElement.value !== "") {
        var newItem = {
          text: this._inputElement.value,
          entryTime: new Date().toLocaleString(),
          entryID: this.state.items.length + 1,
          key: Date.now()
        };

        this.setState((prevState) => {
          return {
            items: prevState.items.concat(newItem)
          };
        });
        this._inputElement.value = "";
      }
      console.log(this.state.items);

      e.preventDefault();
  }

  deleteItem(key) {
    var filteredItems = this.state.items.filter(function (item) {
      return (item.key !== key);
    });

    this.setState({
      items: filteredItems
    });
  }

  render() {
    return (
      <div className="row">
        <div className="col-md text-center mx-auto rounded p-5 m-3">
          <h3><img src="logo192.png" width="30" alt="React Logo" className="img-fluid m-2" id="reactLogo"></img>React To-Do List</h3>
          <hr className="solid"></hr>
          <form onSubmit={this.addItem}>
          <div className="form-group">
          <input ref={(a) => this._inputElement = a} type="text" placeholder="New Entry"></input>
          <button className="btn-outline-dark rounded m-2" type="submit">Add</button>
          </div>
          </form>
          <TodoItems entries={this.state.items}
                    delete={this.deleteItem}/>
        </div>
      </div>
    );
  }
}

export default TodoList;
